import {Carousel,CarouselItem} from 'element-ui';
Vue.use(Carousel);
Vue.use(CarouselItem);

Vue.use(Router)

export default new Router({
	mode: 'hash',//è¯¥æ¨¡å¼ä¸ä¼å°è·¯å¾æ ¼å¼åä¸º #! å¼å¤´history
	scrollBehavior (to, from, savedPosition) {// return æææ»å¨å°åªä¸ªçä½ç½®  
	    if (savedPosition) {
		    return savedPosition
		} else {
		    return { x: 0, y: 0 }
		}
	},
  	routes: [
	    {
	      path: '/',
	      name: 'Home',
	      component: Home
	    }
	]
})



