                <div class="job-btn1" @click="currentworkNum=1"></div>
                <div class="job-btn2" @click="currentworkNum=1"></div>
                <div class="job-btn3" @click="tabChange('contact')"></div>
                <div class="job-btn4">
                  <form action="mailto:contact@taqwa.io" method="post" enctype="text/plain">
                    <input type="submit" />
                  </form>
                </div>
                <div class="job-btn5">
                  <form action="mailto:contact@taqwa.io" method="post" enctype="text/plain">
                    <input type="submit" />
                  </form>
                </div>
              </div>
              <div class="job-box">
                <img src="/static/img/work2-2.png" class="work-img" />
                <div class="job-btn6">
                  <form action="mailto:contact@taqwa.io" method="post" enctype="text/plain">
                    <input type="submit" />
                  </form>
                </div>
              </div>
            </div>
          </div>
        </div>
      </el-carousel-item>
      <el-carousel-item name="contact">
        <div class="tab-contain">
          <div class="head">
            <img src="/static/img/logo.png" class="logo" />
            <div class="menus">
              <div class="the-menu" :class="{'act':currentMenu==item.id}" :name="item.id" v-for="item in menuList" :key="item.id" @click="tabChange(item.id)">{{ $t(item.name) }}</div>
            </div>
            <div class="langs">
              <div class="the-lang" :class="{'act':isopen}" @click.stop="isopen=!isopen">
                <div>{{ langList[langIdx].name }}</div>
                <img src="/static/img/icon_arrow.png" class="arrow" />
                <div class="lang-list">
                  <div class="lang-item" :class="{'act':idx==langIdx}" v-for="item,idx in langList" :key="item.type" @click="changeLang(idx)">{{ item.name }}</div>
                </div>
              </div>
              <img src="/static/img/tip.png" class="tip-img" @click="tabChange('qingzhen')" />
            </div>
          </div>
          <div class="concat-block">
            <div class="concat-page">
              <img src="/static/img/concat-bg-top.png" class="concat-img" />
              <img src="/static/img/concat-bg-bottom.png" class="concat-img" style="z-index: 1;" />
              <div class="bgs-block">
                <!-- <div class="bgs-box" :class="['_scroll'+currentBGS]">
                  <img src="/static/img/concat-bgs1.png" class="concat-img" :class="[currentBGS==0?'doshowcc':'dohidecc']" />
                </div> -->
                <el-carousel ref="bgsRef" class="my-carousel" direction="vertical" :autoplay="false" indicator-position="none" arrow="never">
                  <el-carousel-item>
                    <div class="bgs-box">
                      <img src="/static/img/concat-bgs1.png" class="concat-img" :class="[currentBGS==0?'doshowcc':'dohidecc']" />
                    </div>
                  </el-carousel-item>
                  <el-carousel-item>
                    <div class="bgs-box">
                      <img src="/static/img/concat-bgs2.png" class="concat-img" :class="[currentBGS==1?'doshowcc':'dohidecc']" />
                    </div>
                  </el-carousel-item>
                  <el-carousel-item>
                    <div class="bgs-box">
                      <img src="/static/img/concat-bgs3.png" class="concat-img" :class="[currentBGS==2?'doshowcc':'dohidecc']" />
                    </div>
                  </el-carousel-item>
                  <el-carousel-item>
                    <div class="bgs-box">
                      <img src="/static/img/concat-bgs4.png" class="concat-img" :class="[currentBGS==3?'doshowcc':'dohidecc']" />
                    </div>
                  </el-carousel-item>
                  <el-carousel-item>
                    <div class="bgs-box">
                      <img src="/static/img/concat-bgs5.png" class="concat-img" :class="[currentBGS==4?'doshowcc':'dohidecc']" />
                    </div>
                  </el-carousel-item>
                </el-carousel>
              </div>
              <div class="bgs-btns">
                <div class="bgs-btn" @click="doScroll(-1)"></div>
                <div class="bgs-btn" @click="doScroll(1)"></div>
              </div>
              <div class="map-box" id="googleMap">
                <img src="/static/img/concat-map.png" class="concat-img" v-if="!map">
              </div>
              <div class="qz-btn" @click="tabChange('qingzhen')"></div>
              <div class="concat-btn" @click="toTiktok()"></div>
            </div>
          </div>
        </div>
      </el-carousel-item>
      <el-carousel-item name="qingzhen">
        <div class="tab-contain">
          <div class="head">
            <img src="/static/img/logo1.png" class="logo" />
            <div class="menus">
              <div class="the-menu menu2" :class="{'act':currentMenu==item.id}" :name="item.id" v-for="item in menuList" :key="item.id" @click="tabChange(item.id)">{{ $t(item.name) }}</div>
            </div>
            <div class="langs">
              <div class="the-lang" :class="{'act':isopen}" @click.stop="isopen=!isopen">
                <div>{{ langList[langIdx].name }}</div>
                <img src="/static/img/icon_arrow.png" class="arrow" />
                <div class="lang-list">
                  <div class="lang-item" :class="{'act':idx==langIdx}" v-for="item,idx in langList" :key="item.type" @click="changeLang(idx)">{{ item.name }}</div>
                </div>
              </div>
              <img src="/static/img/tip.png" class="tip-img act" @click="tabChange('qingzhen')" />
            </div>
          </div>
          <div class="qingzhen-block">
            <img src="/static/img/qingzhen-0.png" class="qingzhen-img">
            <img src="/static/img/qingzhen-1.png" class="qingzhen-img">
            <img src="/static/img/qingzhen-2.png" class="qingzhen-img">
            <img src="/static/img/qingzhen-3.png" class="qingzhen-img">
            <img src="/static/img/qingzhen-4.png" class="qingzhen-img">
            <img src="/static/img/qingzhen-5.png" class="qingzhen-img">
          </div>
        </div>
      </el-carousel-item>
    </el-carousel>
  </div>
</template>
<style scoped>
@font-face {
  font-family: BonaNova-Bold;
  font-size: 36px;
  src: url('/static/BonaNova-Bold.ttf');
}
@font-face {
  font-family: BonaNova-Italic;
  font-size: 36px;
  src: url('/static/BonaNova-Italic.ttf');
}
@font-face {
  font-family: BonaNova-Regular;
  font-size: 36px;
  src: url('/static/BonaNova-Regular.ttf');
}
</style>
<script>
export default {
  name: 'Home',
  data () {
    return {
      isopen: false,
      langList: [
        {type:'en',name:'ENG'}
      ],
      langIdx: 0,
      menuList: [
        {id: "home",name:"HOME"},
        {id: "product",name:"PRODUCT"},
        {id: "brand",name:"BRAND"},
        {id: "job",name:"JOB"},
        {id: "contact",name:"CONTACT"}
      ],
      wheeling: false,
      currentMenu: 'home',
      showHomePage: false,
      currentHome: 1,
      currentHomeType: 1,
      productList: [
        {name:"æºè½æè¡¨",models:[{name:"Quran Watch",des:"ç©æ¯æä¸ç¨æè¡¨"},{name:"Fun Pro",des:"è¿å¨&å¥åº· å¤åè½æºè½æè¡¨"}]},
        {name:"æºè½é¾åå¨",des:"å¨çé¦æ¬¾å¤§å±SMART VAPE"}
      ],
      currentProductNum: 1,//äº§å
      currentProductBgNum: 1,//äº§åèæ¯è²
      currentProductColorNum: 1,//èå¸¦è²
      currentProductPMNum: 1,//å±å¹åå®¹
      currentBrandNum: 0,//åçé¡µç 
      currentworkNum: 1,//æè
      map: null,
      currentBGS: 0
    }
  },
  mounted(){
    let that = this;
    setTimeout(() => {
      that.tabChange("home");
    }, 1000);
    $('.contentBody').on('click',function(e) {
      let els = $('.btn-box');
      for (let i = 0; i < els.length; i++) {
        $(els[i]).removeClass('act');
      }
      that.isopen = false;
    });
    window.addEventListener('wheel',function(e){
      // console.log(that.currentMenu,that.wheeling,e.wheelDeltaY>0?'up':'down')
      if(that.currentMenu=='home'&&!that.wheeling){
        that.wheeling = true;
        setTimeout(() => {
          if(e.wheelDeltaY>0){
            if(that.currentHome>1){
              that.currentHome--;
            }
          }else{
            if(that.currentHome<5){
              that.currentHome++;
            }
          }
          that.wheeling = false;
        }, 500);
      }else if(that.currentMenu=='brand'&&!that.wheeling){
        that.wheeling = true;
        setTimeout(() => {
          if(e.wheelDeltaY>0){
            if(that.currentBrandNum>1){
              that.currentBrandNum--;
            }
          }else{
            if(that.currentBrandNum<12){
              that.currentBrandNum++;
            }
          }
          that.wheeling = false;
        }, 500);
      }
    })
  },
  methods: {
    doScroll(type){
      if(type==-1){
        if(this.currentBGS>0){
          this.currentBGS--;
        }else{
          this.currentBGS = 4;
        }
        this.$refs.bgsRef.prev();
      }else if(type==1){
        if(this.currentBGS<4){
          this.currentBGS++;
        }else{
          this.currentBGS = 0;
        }
        this.$refs.bgsRef.next();
      }
    },
    brandChangePage(){
      if(this.currentBrandNum<12){
        this.currentBrandNum++;
      }else{
        this.currentBrandNum = 1;
      }
    },
    changeWatchBG(type){
      if(this.currentProductBgNum==1){
        if(type==1){
          this.currentProductBgNum = 2;
        }else{
          this.currentProductBgNum = 3;
        }
      }else if(this.currentProductBgNum==2){
        if(type==1){
          this.currentProductBgNum = 1;
        }else{
          this.currentProductBgNum = 3;
        }
      }else if(this.currentProductBgNum==3){
        if(type==1){
          this.currentProductBgNum = 1;
        }else{
          this.currentProductBgNum = 2;
        }
      }
    },
    changeLang(idx){
      this.langIdx = idx;
    },
    showList(id){
      let els = $('.btn-box');
      for (let i = 0; i < els.length; i++) {
        $(els[i]).removeClass('act');
      }
      $('.btn-box.mybtn'+id).addClass('act');
    },
    tabChange(menu){//å¤´é¨å¯¼èª
      if(this.currentMenu!=menu){
        this.currentMenu = menu;
        this.$refs.carouselRef.setActiveItem(menu);
      }
      this.wheeling = false;
      if(menu=='home'){
        let that = this;
        that.currentHome = 1;
        that.currentHomeType = 1;
        setTimeout(() => {
          that.showHomePage = true;
        }, 800);
      }else{
        this.showHomePage = false;
      }
      if(menu=='concat'){
        if(google&!this.map){
          var mapProp = {
              center: new google.maps.LatLng(-6.184757360602209, 106.78518602700933),
              zoom:15,
              mapTypeId:google.maps.MapTypeId.ROADMAP
          };
          this.map = new google.maps.Map(document.getElementById("googleMap"), mapProp);
          var marker = new google.maps.Marker({
            position: new google.maps.LatLng(-6.184757360602209, 106.78518602700933),
          });
          marker.setMap(this.map);
        }
      }
      if(menu=='brand'){
        this.currentBrandNum = 1;
      }else{
        this.currentBrandNum = 0;
      }
    },
    toShopee(){
      window.open('https://shopee.co.id/taqwasmartwatch?categoryId=100013&entryPoint=ShopByPDP&itemId=25679391607');
    },
    toTiktok(){
      window.open('https://vt.tokopedia.com/t/ZSYgN5or4/');
    },
  }
}
</script>


